import {
  ComponentProps,
  withStreamlitConnection,
  StreamlitComponentBase,
  Streamlit,
} from 'streamlit-component-lib'
import React, { ReactNode } from 'react'

import styles from './style.module.scss';

// Define here the available hosts
enum Platform {
  sketchup,
  rhino,
  revit
}

interface Options {
  layer: string
  units: string
}

interface State {
  action: string
  data: string
  uniqueId: string
  options: Options
  platform: string
  isFocused: boolean
}

interface Labels {
  [key: string]: string;
}

declare global {
  interface Window {
    chrome: any;
    sketchup: any;
  }
}

class SendGeometry extends StreamlitComponentBase<State> {

  public constructor(props: ComponentProps) {
    super(props)

    // Values from user input
    const action = this.props.args['action'] as string;
    const data = this.props.args['data'] as string;
    const uniqueId = this.props.args['uniqueId'] as string;
    const options = this.props.args['options'] as Options;
    const platform = this.props.args['platform'] as string;

    const initialState = {
      action: action,
      data: data,
      uniqueId: uniqueId,
      options: options,
      platform: platform.toLowerCase()
    } as State

    this.state = {
      ...initialState
    }
  }

  public render = (): ReactNode => {
    let labels: Labels = {
      'BakeGeometry': 'Bake Geometry',
      'ClearGeometry': 'Clear Geometry',
      'DrawGeometry': 'Draw Geometry',
      'DisableDraw': 'Disable Draw',
      'BakePollinationModel': 'Bake Pollination Model'
    }

    const imgStyle = {
      margin: '0px 3px 2px 0px'
    }

    return (
      <span>
        { this.props.args['action'] in labels && (
        <button
          className={styles.poButton}
          onClick={this._onClicked}
          disabled={this.props.disabled}
          onFocus={this._onFocus}
          onBlur={this._onBlur}
        >
          <span>
            <img style={imgStyle} src={process.env.PUBLIC_URL + '/img/pollination.png'} alt='logo' />
          </span>
          { labels[this.props.args['action']] }
        </button>
        )}
      </span>
    )
  }

  private _onClicked = (): void => {
    const platform = this.props.args['platform'] as string;
    let newState = { platform: platform.toLowerCase() } as State;
    if (platform.toLowerCase() == Platform[Platform.rhino]) { 
      newState = this._dotNetRun(); 
    } else if (platform.toLowerCase() == Platform[Platform.sketchup]) {
      newState = this._rubyRun(); 
    }
    this._setStateNow(newState);
  }

  private _rubyRun = (): State => {
    if (typeof window.parent.sketchup != 'undefined') 
    {
      var message = {
        action: this.props.args['action'],
        data: this.props.args['data'],
        uniqueId: this.props.args['uniqueId'],
        options: this.props.args['options']
      };
      var json = JSON.stringify(message);
      window.parent.sketchup.send_geometry(json);

      const platform = this.props.args['platform'] as string;

      var newState = {
        action: message.action,
        data: message.data,
        uniqueId: message.uniqueId,
        options: message.options,
        platform: platform.toLowerCase()
      } as State;
  
      console.log('[POLLINATION-DEBUG]: JSON string sent.');
      return newState;
    }
    else {
      console.log('[POLLINATION-DEBUG]: sketchup not found.');
      return this.state;
    }
  }

  private _dotNetRun = (): State => {
    if (typeof window.parent.chrome != 'undefined') {
      if (typeof window.parent.chrome.webview == 'undefined')
      {
        console.log('[POLLINATION-DEBUG]: webview not found.');
        return this.state;
      }
      
      var message = {
        action: this.props.args['action'],
        data: this.props.args['data'],
        uniqueId: this.props.args['uniqueId'],
        options: this.props.args['options']
      };
      var json = JSON.stringify(message);
      window.parent.chrome.webview.postMessage(json);

      const platform = this.props.args['platform'] as string;

      var newState = {
        action: message.action,
        data: message.data,
        uniqueId: message.uniqueId,
        options: message.options,
        platform: platform.toLowerCase()
      } as State;
  
      console.log('[POLLINATION-DEBUG]: JSON string sent.');
      return newState;
    }
    else
    {
      console.log('[POLLINATION-DEBUG]: chrome not found.');
      return this.state;
    }
  }

  private _setStateNow = (newState: State): void => {
    this.setState(
      (prevState) => ({ 
        ...prevState, 
        action: newState.action,
        data: newState.data,
        uniqueId: newState.uniqueId,
        options: newState.options
      }),
      () => Streamlit.setComponentValue(this.state.action)
    );
  }

  private _onFocus = (): void => {
    this.setState({ isFocused: true })
  }

  private _onBlur = (): void => {
    this.setState({ isFocused: false })
  }
}

export default withStreamlitConnection(SendGeometry)
